import React from "react";
import { TextField } from "@material-ui/core";

const FormInput = ({ label, func, name, value }) => {
  return (
    <TextField
      onChange={func}
      name={name}
      value={value}
      variant='outlined'
      color='primary'
      label={label}
      type={name}
      className='form__input'
    />
  );
};

export default FormInput;
