import React, { useState } from "react";
import { TextField, Button } from "@material-ui/core";
import FormInput from "./FormInput";
import { db } from "../firebase/firebase";
import { connect } from "react-redux";
import { handleAddPost } from "../redux/actions/postsActions";

const Panel = ({ handleAddPost }) => {
  const [image, setImage] = useState("");
  const [title, setTitle] = useState("");
  const [intro, setIntro] = useState("");
  const [tags, setTags] = useState("");
  const [content, setContent] = useState("");

  const handleInput = (e) => {
    const { value, name } = e.target;
    if (name === "img") setImage(value);
    if (name === "title") setTitle(value);
    if (name === "intro") setIntro(value);
    if (name === "tags") setTags(value);
    if (name === "content") setContent(value);
  };

  const randomId = () => Math.floor(Math.random() * 9999999999);

  return (
    <form
      className='adminPage__form'
      onSubmit={async (e) => {
        e.preventDefault();
        const post = {
          title,
          intro,
          tags,
          image,
          content,
          date: "22 January",
          author: "msT Lucius",
        };
        console.log(post);
        await db.collection("posts").add(post);
        handleAddPost(post);
        setImage("");
        setTitle("");
        setIntro("");
        setTags("");
        setContent("");
      }}
    >
      <h3>Panel</h3>
      <FormInput
        label='Image'
        value={image}
        name='img'
        func={(e) => handleInput(e)}
      />
      <FormInput
        label='Title'
        value={title}
        name='title'
        func={(e) => handleInput(e)}
      />
      <FormInput
        label='Intro'
        value={intro}
        name='intro'
        func={(e) => handleInput(e)}
      />
      <FormInput
        label='Tags'
        value={tags}
        name='tags'
        func={(e) => handleInput(e)}
      />
      <TextField
        multiline
        variant='outlined'
        color='primary'
        label='Content'
        rows={5}
        className='form__input'
        value={content}
        name='content'
        onChange={(e) => handleInput(e)}
      />
      <Button variant='contained' color='primary' size='large' type='submit'>
        Add Post
      </Button>
    </form>
  );
};

export default connect(null, { handleAddPost })(Panel);
