import React from "react";
import Post from "./Post";

const Posts = ({ posts }) => {
  return (
    <main className='section posts'>
      {posts.map(
        ({ id, image, title, date, author, avatar, intro, content, tags }) => {
          return (
            <Post
              id={id}
              key={id}
              image={image}
              title={title}
              date={date}
              author={author}
              avatar={avatar}
              intro={intro}
              content={content}
              tags={tags}
            />
          );
        }
      )}
    </main>
  );
};

export default Posts;
